import wPicker from "@/components/w-picker/w-picker.vue"; 
import plateInput from '@/components/uni-plate-input/uni-plate-input.vue'
import anUploadImg from '@/components/an-uploadImg/an-uploadImg.vue'
import linkUrl from '@/components/link/link.vue'
import banner from '@/components/banner/banner.vue'
import contact from '@/components/contact/contact.vue'
export default {
	components: {
		plateInput,
		anUploadImg,
		wPicker,
		linkUrl,
		banner,
		contact
	},
	data() {
		return {
			title: 'Hello',
			car:{
				"carNo":"",
				"phone":"",
				"msgNotify":0,
				"msgContent":"",
				"wechatImg":null,
				"intervalNotify":0,
				"receiveVoice":0,
				"carBrand":"",
				"carType":"",
				"buyTime":"",
				"commonlyUsedPlace":"",
				"qrcodeTemplateId":1
			},
			linkList:[
				{
					label:"广汽三菱",
					value:1,
					children:[
						{
							label:"劲炫ASX",
							value:1001
						},
						{
							label:"欧蓝德",
							value:1002
						},
						{
							label:"奕歌",
							value:1003
						},
						{
							label:"帕杰罗·劲畅",
							value:1004
						},
						{
							label:"帕杰罗",
							value:1005
						}
					]
				},
				{
					label:"三菱(进口)",
					value:2,
					children:[
						{
							label:"帕杰罗(进口)",
							value:2001
						},
						{
							label:"ASX劲炫(进口)",
							value:2002
						},
						{
							label:"欧蓝德(进口)",
							value:2003
						}
					]
				},
				{
					label:"东南汽车",
					value:3,
					children:[
						{
							label:"蓝瑟",
							value:3001
						},
						{
							label:"戈蓝",
							value:3002
						},
						{
							label:"翼神",
							value:3003
						}
					]
				}
			],
			plateShow:false,
			read:false,
			wx:"wx",
			time:60,
			timeState:false,
			token:"",
			code:""
			
		}
	},
	onLoad(){

	},
	methods: {
		onConfirm2(brand){
			this.car.carBrand = brand.checkArr[0];
			this.car.carType = brand.checkArr[1]
		},
		getCarBrand(){
			let _this = this;
			this.$ajax("/car/brand/page","POST",{pageNo:1,pageSize:99}).then(res=>{
				_this.linkList = res.list
			})
		},
		chooseCarBrand(){
			this.$refs.linkage.show();
		},
		openDate(){
			this.$refs.picker1.show();
		},
		onConfirm1(date){
			console.log(date);
			this.car.buyTime = date.result
		},
		openAddress(){
			this.$refs.picker.show();
		},
		onConfirm(address){
			this.car.commonlyUsedPlace = address.result
		},
		getCode(){
			// if(){
				
			// }
			// this.$ajax("/sms/send",{number:this.phone})
		},
		changeIntervalNotify(e){
			if(e.target.value){
				this.car.intervalNotify = 1
			}else{
				this.car.intervalNotify = 0
			}
		},
		changeIntervalVoice(e){
			if(e.target.value){
				this.car.receiveVoice = 1
			}else{
				this.car.receiveVoice = 0
			}
		},
		changeMsgNotify(e){
			if(e.target.value){
				this.car.msgNotify = 1
			}else{
				this.car.msgNotify = 0;
				this.car.msgContent = "";
			}
			
		},
		refreshImageList(list){
			this.car.wechatImg = list;
		},
		setPlate(plate){
			if(plate.length >= 7) this.car.carNo = plate;
			console.log(this.car.carNo);
			this.plateShow = false
		},
		xy(){
			uni.navigateTo({
				"url":"/pages/xy/xy"
			})
		},
		toUrl(url){
			uni.navigateTo({
				"url":url
			})
		},
		pay(){
			let _this = this;
			if(_this.car.carNo == ""){
				uni.showToast({
					title:"车牌号不能为空",
					"icon":"none"
				})
				return false;
			}
			if(_this.car.phone == ""){
				uni.showToast({
					title:"手机号不能为空",
					"icon":"none"
				})
				return false;
			}
			if(!(/^1[3456789]\d{9}$/.test(_this.car.phone))){
				uni.showToast({
					"title":"电话号码格式不正确",
					"icon":"none"
				}) 
				return false; 
			}
			
			
			if(this.read){
				if(this.car.wechatImg == null){
					this.car.wechatImg = [];
				}
				this.$ajax("/move/apply","POST",this.car).then(res=>{
					// uni.setStorageSync("carMessage",JSON.stringify(res));
					uni.reLaunch({
						"url":"/pages/address/msnc/qcodesuccess?qrcode="+res
					})
					
				})

			}else{
				uni.showToast({
					"title":"请先勾选已阅读协议内容",
					"icon":"none"
				})
			}
			
		}
	}
	
}
